.App {
  text-align: center;
  min-height: 100vh;
  padding-bottom: 7%;
  font-family: "Syne Mono", monospace;
  justify-content: center;
}

* {
  padding: 0;
  margin: 0;
}



header {
  padding-bottom: 50px;
  display: flex;
  align-items: flex-start;
}

body {
  background-image: url("../public/assets/6.svg");
  background-position: 100% 100%;
  background-size: cover;
  background-attachment: fixed;
  background-repeat:unset;
  height: 100vh;
  padding: 1%;
  margin: 2%; 
  font-size: 120%;
 
}

.text {
  color: rgb(237, 237, 224);
  align-self: center;
  line-height: 30px;
  text-align: center;
  width: 70%;
  margin-left: 15%;
}

.navbar {
  position: fixed;
  padding-top: -15%;
  margin: -2%;
  z-index: 2;
}

.navbar button:hover {
  background-color: rgb(248, 205, 126);
}

.btn:hover {
  background-color: rgb(248, 205, 126);

}

.nav-active button {
  background-color: rgb(250, 190, 80);
  padding: 10px;
  font-size: 20px;
  border-radius: 15px;
}

.nav-inactive button {
  background-color: rgba(255, 166, 0, 0.661);
  padding: 8px;
  font-size: 15px;
  border-radius: 10px;
}

.navbar > div {
  margin-left: 10px;
  margin-right: 10px;
}

.btn {
  background-color: rgba(255, 166, 0, 0.661);
  padding: 8px;
  font-size: 18px;
  border-radius: 3px;
  margin-bottom: 10px;
  font-family: "VT323", monospace;
}

.flex_container {
  display: flex;
  padding: 2%;
  justify-content:space-between;
  text-align: justify;
}

.description {
background-color: aliceblue;
width: 40%;
border-radius: 10px;
padding: 20px;
}

.container_card {
  padding: 20px;
  padding-bottom: 40px;
  margin: auto;
  margin-top: 60px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  
}

.card {
  width: 300px;
  position: relative;
  margin: 20px;
border-radius: 20px;

}

.card:hover{
  box-shadow: 20px 20px 20px -5px;
  transition: all 400ms cubic-bezier(0.5,0,0,1);
  
}

.card_image {
  width: 100%;
  height: 220px;
  padding: 5px;
  display: flex;
  background-color: aliceblue;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 1;
  transition: all 400ms cubic-bezier(0.5,0,0,1);
  border-radius: 20px;
}
.card_image img {
  width: 150px;
  border-radius: 10px 10px 10px 10px;
padding-right: 5px;
}


.card:hover .card_description {
  margin-top: 0px;
  border-radius: 0px 0px 20px 20px;
background-color: rgb(215, 182, 46);

}
.card:hover .card_image {
margin-top: -50px;
border-radius: 20px 20px 0px 0px;
background-color: rgb(215, 182, 46);


}

.card_description {
  width: 100%;
  height: 220px;
  background-color: aliceblue;
  padding: 5px;
  margin-top: -220px;
  transition: all 400ms cubic-bezier(0.5,0,0,1);
  border-radius: 20px;
}

.card_description:hover {
margin-top: 0;
}

.card_description h4{
font-size: 14px;
margin: 5px;
display: box;
text-align: justify;
box-orient: vertical;
line-clamp: 3;
overflow: hidden;
}

.card_description input {
  margin-top: 20px;
  padding: 10px 30px;
  cursor: pointer;
  border: none;
  border-radius: 20px;
  box-shadow: 1px 1px 10px -5px;
  transition: box-shadow 500ms;
}

.card_description input:hover {
  box-shadow: 2px 2px 20px -5px;
}

.card input {
  visibility: hidden;
  transition: all 400ms cubic-bezier(0.5,0,0,1);
  width: fit-content;

}
.card:hover input {
  visibility:visible;
  transition: all 400ms cubic-bezier(0.5,0,0,1);

}
.comp {
  display: flex;
  align-items: center;
  background-color: aliceblue;
  border-radius: 150px 50px 50px 150px;
  max-width: 50%;
transition: all 400ms cubic-bezier(0.5,0,0,1);
margin: 30px auto;

}
.comp p{
visibility: hidden;
padding: 5px;
font-size: 5px;

}
.comp:hover {
background-color: rgb(215, 182, 46);
box-shadow: 20px 20px 20px -5px;
transition: all 400ms cubic-bezier(0.5,0,0,1);
max-width: 100%;

}
.comp:hover p {
  transition: all 400ms cubic-bezier(0.5,0,0,1);
  visibility: visible;
  font-size: 20px;

}
.comp h3 {
  font-size: 30px;
  transition: all 400ms cubic-bezier(0.5,0,0,1);
}
.comp:hover h3{
  font-size: 20px;
transition: all 400ms cubic-bezier(0.5,0,0,1);
}

.description h3 {
font-size: 30px;
text-align: center;
transition: all 400ms cubic-bezier(0.5,0,0,1);


}

.description:hover h3 {
transition: all 400ms cubic-bezier(0.5,0,0,1);
  font-size: 35px;
  text-align: center;
  }
.description:hover {
  background-color: rgb(215, 182, 46);
  box-shadow: 20px 20px 20px -5px;
transition: all 400ms cubic-bezier(0.5,0,0,1);

}
.description p {
transition: all 100ms cubic-bezier(0.5,0,0,1);
  font-size: 18px;
  padding: 10px;
}
.description:hover p{
transition: all 400ms cubic-bezier(0.5,0,0,1);
  visibility: visible;
  font-size: 23px;
}











































